.apl-admin-file-uploader-list {
  position: absolute;
  overflow-y: auto;
  overflow-x: hidden;
  width: auto;
  height: auto;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  padding: 15px;
}
.apl-admin-file-uploader-item {
  width: 100%;
  clear: both;
  overflow: hidden;
  cursor: default;
  position: relative;
  margin-bottom: 15px;
}
.apl-admin-file-uploader-metadata {
  width: 100%;
  clear: both;
  overflow: hidden;
  color: #000;
  cursor: default;
  height: 21px;
}
.apl-admin-file-uploader-name {
  height: 21px;
  overflow: hidden;
  width: 50%;
  -ms-text-overflow: ellipsis;
  text-overflow: ellipsis;
  line-height: 19px;
  white-space: nowrap;
  float: left;
  position: relative;
}
.apl-admin-file-uploader-type {
  height: 21px;
  overflow: hidden;
  width: 19%;
  -ms-text-overflow: ellipsis;
  text-overflow: ellipsis;
  line-height: 19px;
  white-space: nowrap;
  float: left;
  padding-left: 15px;
  position: relative;
}
.apl-admin-file-uploader-size {
  height: 21px;
  overflow: hidden;
  width: 30%;
  -ms-text-overflow: ellipsis;
  text-overflow: ellipsis;
  line-height: 19px;
  white-space: nowrap;
  float: left;
  text-align: right;
  padding-left: 15px;
  position: relative;
}
.apl-admin-file-uploader-progress {
  width: 100%;
  clear: both;
  overflow: hidden;
  color: #000;
  cursor: default;
  height: 21px;
  margin-bottom: 0;
  display: none;
}
.apl-admin-file-uploader-progress-bar {
  overflow: hidden;
  width: 0%;
  -ms-text-overflow: ellipsis;
  text-overflow: ellipsis;
  line-height: 20px;
  white-space: nowrap;
  text-align: center;
}
.apl-admin-file-uploader-status {
  height: 21px;
  overflow: hidden;
  width: 99.6%;
  -ms-text-overflow: ellipsis;
  text-overflow: ellipsis;
  line-height: 19px;
  white-space: nowrap;
  float: left;
  position: relative;
}
.apl-admin-file-uploader-success {
  color: #090;
  font-weight: bold;
}
.apl-admin-file-uploader-fail {
  color: #d00;
}
.apl-admin-file-uploader-clipboard {
  border-bottom: 1px #333 dashed;
  cursor: pointer;
}