.version {
  content: "12345";
}

* {
  font-family: "Roboto", sans-serif;
  font-weight: 300;
  font-weight: 500;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

* + * {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

*::after, *::before {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

.sf-dump {
  z-index: 10000000 !important;
}

.sf-toolbarreset {
  bottom: -13px !important;
  transition: bottom 0.2s ease 0s;
}

.sf-toolbarreset:hover {
  bottom: 0 !important;
  transition: bottom 0.2s ease 0s;
}

.bootstrap-datetimepicker-widget * {
  border: 0 !important;
}

.cke_reset_all, .cke_reset_all *, .cke_reset_all a, .cke_reset_all textarea {
  font-family: "Roboto", sans-serif !important;
}

span.cke_button_icon.cke_button__emojione_icon {
  background-image: url("https://admin.oytoy.ru/static/ckeditor/plugins/icons.png?t=J6IA") !important;
  background-position: 0 -11008px !important;
  background-size: auto !important;
}

html {
  width: 100%;
  height: 100%;
  overflow: hidden;
  font-size: 14px;
}

body {
  font-family: "Roboto", sans-serif;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

#aplab-admin-content {
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  width: auto;
  background: #fff none repeat scroll 0 0;
  overflow: hidden;
  z-index: 200;
}

#aplab-admin-open-launcher {
  position: absolute;
  left: 0;
  top: 0;
  width: 50px;
  height: 50px;
  background: #323232;
  border: 0;
  color: #fff;
  font-size: 28px;
  text-align: center;
  overflow: hidden;
  padding: 0;
  margin: 0;
  line-height: 49px;
  outline-style: none;
  cursor: pointer;
}

.apl-admin-launcher-is-opened #aplab-admin-open-launcher {
  display: none;
}

#aplab-admin-open-action-menu {
  position: absolute;
  right: 0;
  top: 0;
  width: 50px;
  height: 50px;
  background: #323232;
  border: 0;
  color: #fff;
  font-size: 18px;
  text-align: center;
  overflow: hidden;
  padding: 0;
  margin: 0;
  line-height: 49px;
  outline-style: none;
  cursor: pointer;
}

#aplab-admin-navbar {
  position: absolute;
  left: 0;
  right: 0;
  width: 100%;
  height: 50px;
  top: 0;
  bottom: auto;
  z-index: 200;
  color: #fff;
  background: #323232;
}

#aplab-admin-workarea {
  position: absolute;
  left: 0;
  right: 0;
  width: 100%;
  top: 50px;
  height: auto;
  bottom: 0;
  overflow: hidden;
  z-index: 100;
}

#aplab-admin-toolbar-wrapper {
  position: absolute;
  left: 50px;
  right: 50px;
  top: 0;
  bottom: 0;
  width: auto;
  height: auto;
  overflow: hidden;
  -webkit-transition: left 0.15s ease 0s;
  -o-transition: left 0.15s ease 0s;
  transition: left 0.15s ease 0s;
}

.apl-admin-launcher-is-opened .apl-admin-launcher {
  z-index: 300;
}

.apl-admin-launcher-is-visible .apl-admin-launcher {
  opacity: 1;
}

.apl-admin-launcher {
  display: block;
  transition: opacity 0.15s ease-in-out;
  opacity: 0;
  z-index: 1;
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  width: auto;
  height: auto;
  overflow: hidden;
  background-color: #393F4C;
  background-image: url("/static/login.jpg");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
}
.apl-admin-launcher-header {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: auto;
  width: auto;
  height: 50px;
  z-index: 100;
}
.apl-admin-launcher-footer {
  position: absolute;
  left: 0;
  right: 0;
  top: auto;
  bottom: 0;
  width: auto;
  height: 50px;
  z-index: 200;
}
.apl-admin-launcher-footer-nav {
  width: 100%;
}
.apl-admin-launcher-footer-nav i {
  color: #fff;
  display: block;
  width: 50px;
  height: 25px;
  overflow: hidden;
  text-align: center;
  line-height: 25px;
  font-size: 8px;
  cursor: pointer;
}
.apl-admin-launcher-footer-nav i.active {
  font-size: 15px;
}
.apl-admin-launcher-close {
  position: absolute;
  left: 0;
  top: 0;
  width: 50px;
  height: 50px;
  border: 0;
  color: #fff;
  font-size: 28px;
  text-align: center;
  overflow: hidden;
  padding: 0;
  margin: 0;
  line-height: 49px;
  outline-style: none;
  cursor: pointer;
}
.apl-admin-launcher-body {
  left: 0;
  right: 0;
  width: auto;
  height: auto;
  top: 50px;
  bottom: 50px;
  position: absolute;
  z-index: 300;
  overflow: hidden;
}
.apl-admin-launcher-page {
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.apl-admin-launcher-items {
  max-width: 1200px;
  margin: 0 auto;
  position: relative;
  height: 100%;
  overflow: hidden;
}
@media (orientation: landscape) {
  .apl-admin-launcher-item {
    float: left;
    width: 16.6%;
    height: 25%;
  }
}
@media (orientation: portrait) {
  .apl-admin-launcher-item {
    float: left;
    width: 25%;
    height: 16.6%;
  }
}
.apl-admin-launcher .owl-stage-outer, .apl-admin-launcher .owl-stage, .apl-admin-launcher .owl-item {
  height: 100%;
}
.apl-admin-launcher-item:hover {
  color: #fff;
  text-decoration: none !important;
  opacity: 0.9;
  background-color: rgba(0, 0, 0, 0.05);
}
.apl-admin-launcher-item {
  color: #fff;
  position: relative;
  padding: 3px;
  overflow: hidden;
  display: block;
  outline-style: none;
  text-shadow: 0 1px 0 rgba(0, 0, 0, 0.5), 0 -1px 0 rgba(0, 0, 0, 0.5), 1px 0 0 rgba(0, 0, 0, 0.5), -1px 0 0 rgba(0, 0, 0, 0.5) !important;
  text-decoration: none !important;
  opacity: 0.7;
  transition: opacity 200ms;
}
.apl-admin-launcher-item-icon {
  text-align: center;
  font-size: 8vmin;
  line-height: 9vmin;
  overflow: hidden;
}
@media (max-height: 320px) {
  .apl-admin-launcher-item-icon {
    text-align: center;
    font-size: 5vmin;
    line-height: 5vmin;
    overflow: hidden;
    max-height: 40%;
  }
}
@media (max-height: 480px) and (min-height: 321px) {
  .apl-admin-launcher-item-icon {
    text-align: center;
    font-size: 6vmin;
    line-height: 6vmin;
    overflow: hidden;
    max-height: 40%;
  }
}
.apl-admin-launcher-item-label {
  text-align: center;
  line-height: 20px;
  height: 21px;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  font-size: calc(9px + 0.5vmin);
}