.apl-admin-toolbar {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  overflow: hidden;
  display: block;
  padding-top: 2px;
}
.apl-admin-toolbar-item {
  display: block;
  float: right;
  height: 46px;
  overflow: hidden;
  color: #fff;
  border: 1px transparent solid;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  border-radius: 5px;
  padding: 0 5px;
  cursor: pointer;
}
.apl-admin-toolbar-item:hover {
  border-color: #757B80;
  background-color: #464A4D;
  color: #fff;
}
.apl-admin-toolbar-icons {
  display: block;
  overflow: hidden;
  height: 24px;
  width: auto;
  position: relative;
}
.apl-admin-toolbar-icon {
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  width: auto;
  height: 32px;
  text-align: center;
  line-height: 31px;
  font-size: 18px;
}
.apl-admin-toolbar-text {
  display: block;
  overflow: hidden;
  height: 18px;
  width: auto;
  line-height: 17px;
  font-size: 11px;
}
.apl-admin-toolbar a {
  text-decoration: none;
}
.apl-admin-toolbar a:hover {
  color: #fff;
}