.apl-data-table {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  overflow: hidden;
  background: #fff;
}
.apl-data-table-scrollbar-calc-outer {
  z-index: 1;
  width: 200px;
  height: 200px;
  position: relative;
  overflow: scroll;
  display: block;
  visibility: hidden;
}
.apl-data-table-scrollbar-calc-inner {
  display: block;
  width: auto;
  height: 100%;
}
.apl-data-table-body {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 52px;
  overflow: hidden;
  z-index: 200;
  background: #f8f8f8;
}
.apl-data-table-scroll-horizontal-wrapper {
  position: absolute;
  left: 38px;
  bottom: 0;
  right: 0;
  top: auto;
  height: 0;
  overflow: hidden;
}
.apl-data-table-scroll-horizontal {
  position: absolute;
  left: 0;
  bottom: 0;
  right: 0;
  top: auto;
  height: 200px;
  overflow-y: hidden;
  overflow-x: auto;
}
.apl-data-table-scroll-horizontal-content {
  height: 10px;
  width: auto;
}
.apl-data-table-scroll-vertical-wrapper {
  position: absolute;
  right: 0;
  top: 38px;
  bottom: 0;
  width: 0;
  left: auto;
  overflow: hidden;
}
.apl-data-table-scroll-vertical {
  position: absolute;
  right: 0;
  top: 0;
  bottom: 0;
  width: 200px;
  left: auto;
  overflow-x: hidden;
  overflow-y: auto;
}
.apl-data-table-scroll-vertical-content {
  width: 10px;
  height: auto;
}
.apl-data-table-content {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
}
.apl-data-table-header {
  position: absolute;
  left: 38px;
  right: 0;
  top: 0;
  height: 38px;
  z-index: 100;
  overflow: hidden;
  background: #fff;
}
.apl-data-table-header > div {
  border: 0;
  overflow: hidden;
  clear: both;
  position: absolute;
  left: 0;
  top: 0;
}
.apl-data-table-header > div > div {
  height: 38px;
  border: 1px solid #ddd;
  border-left: 0;
  float: left;
  padding: 8px;
  overflow: hidden;
  white-space: nowrap;
  -ms-text-overflow: ellipsis;
  text-overflow: ellipsis;
  background: #f8f8f8;
}
.apl-data-table-sidebar {
  position: absolute;
  left: 0;
  top: 0;
  width: 38px;
  bottom: 0;
  background: #fff;
}
.apl-data-table-sidebar-header {
  border: 1px solid #ddd;
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  height: 38px;
  padding: 8px 0;
  text-align: left;
  background: #f8f8f8;
  overflow: hidden;
}
.apl-data-table-sidebar-header .custom-checkbox label {
  left: 10px;
}
.apl-data-table-sidebar-body {
  position: absolute;
  left: 0;
  top: 38px;
  right: 0;
  bottom: 0;
  overflow: hidden;
}
.apl-data-table-sidebar-body > div {
  position: absolute;
  left: 0;
  top: 0;
  width: 38px;
}
.apl-data-table-sidebar-body > div > div {
  border: 1px solid #ddd;
  border-top: 0;
  padding: 8px 0;
  text-align: left;
  overflow: hidden;
  -ms-text-overflow: ellipsis;
  background: #f8f8f8;
  position: relative;
}
.apl-data-table-sidebar-body > div > div .custom-checkbox label {
  left: 10px;
}
.apl-data-table-data-wrapper {
  position: absolute;
  left: 38px;
  top: 38px;
  right: 0;
  bottom: 0;
  overflow: hidden;
  background: #fff;
}
.apl-data-table-data {
  position: absolute;
  left: 0;
  top: 0;
  overflow: hidden;
}
.apl-data-table-data > div {
  border: 0;
  overflow: hidden;
  clear: both;
}
.apl-data-table-data > div > div {
  border: 1px solid #ddd;
  border-top: 0;
  border-left: 0;
  float: left;
  padding: 8px;
  overflow: hidden;
  white-space: nowrap;
  -ms-text-overflow: ellipsis;
  text-overflow: ellipsis;
  min-height: 37px;
}
.apl-data-table-data > div > div a {
  text-decoration: underline;
}
.apl-data-table-data > div .cell-icon {
  text-align: center;
}
.apl-data-table-data > div > div.i {
  background: transparent center center no-repeat;
  background-size: cover;
}
.apl-data-table-data > div > div.ip-address {
  white-space: pre;
  font-family: Consolas, Menlo, Monaco, Lucida Console, Liberation Mono, DejaVu Sans Mono, Bitstream Vera Sans Mono, Courier New, monospace, serif;
  text-align: center;
}
.apl-data-table-data > div.fat-row > div {
  min-height: 64px;
}
.apl-data-table-data > div:hover {
  background-color: rgba(0, 0, 0, 0.075);
}
.apl-data-table-data > .apl-data-table-active {
  background-color: rgba(0, 0, 0, 0.05);
}
.apl-data-table-data > div.apl-data-table-active:hover {
  background-color: rgba(0, 0, 0, 0.125);
}
.apl-data-table-footer {
  position: absolute;
  left: 0;
  bottom: 0;
  right: 0;
  height: 52px;
  overflow: hidden;
  z-index: 300;
  background: #f8f8f8;
  border-top: 1px #ddd solid;
}
.apl-data-table-prev {
  display: block;
  width: 14.9%;
  overflow: hidden;
  height: 52px;
  float: left;
  text-align: center;
  line-height: 52px;
  cursor: pointer;
}
.apl-data-table-prev.disabled {
  opacity: 0.3;
  cursor: default;
}
.apl-data-table-page {
  display: block;
  width: 34.9%;
  overflow: hidden;
  height: 52px;
  float: left;
  text-align: center;
  padding: 8px;
}
.apl-data-table-page select {
  text-align: center;
  padding-left: 0;
  padding-right: 0;
}
.apl-data-table-next {
  display: block;
  width: 14.9%;
  overflow: hidden;
  height: 52px;
  float: left;
  text-align: center;
  line-height: 52px;
  cursor: pointer;
}
.apl-data-table-next.disabled {
  opacity: 0.3;
  cursor: default;
}
.apl-data-table-limit {
  display: block;
  width: 34.9%;
  overflow: hidden;
  height: 52px;
  float: left;
  text-align: center;
  padding: 8px;
}
.apl-data-table-limit select {
  text-align: center;
  padding-left: 0;
  padding-right: 0;
}

@media (min-width: 480px) {
  .apl-data-table-prev::after {
    content: "Prev";
  }
  .apl-data-table-next::before {
    content: "Next";
  }
  .apl-data-table-limit select {
    display: block;
    width: 59.9%;
    float: left;
    max-width: 120px;
  }
  .apl-data-table-limit::before {
    content: "Limit:";
    display: block;
    width: 39.9%;
    float: left;
    text-align: right;
    line-height: 34px;
    padding-right: 8px;
  }
  .apl-data-table-page select {
    display: block;
    width: 59.9%;
    max-width: 120px;
    float: left;
  }
  .apl-data-table-page::before {
    content: "Page:";
    display: block;
    width: 39.9%;
    float: left;
    text-align: right;
    line-height: 34px;
    padding-right: 8px;
  }
}